.Card {
  width: inherit;
  height: 650px;
  background-color: rgb(255, 255, 255);
  padding: 20px;
  color: rgb(0, 0, 0);
}

.FirstCard {
  width: inherit;
  height: 650px;

  background-image: url('../../../../assets/Content/FirstCard/FirstCard.jpeg');
  background-size: 120%;
  background-position-y: -310px;
  background-attachment: scroll;

  padding: 20px;
  color: rgb(255, 255, 255);
  border: 1px rgb(0, 0, 0);
}



